import React from 'react';
import glamorous from 'glamorous';
import mediaQueries from '../utilities/mediaQueries';

export default glamorous.h1({
  fontSize: 64,
  fontWeight: 700,
  lineHeight: '55px',
  margin: 0,
  [mediaQueries.medium]: {
    fontSize: 54,
    lineHeight: '46px',
  },
  [mediaQueries.small]: {
    fontSize: 36,
    lineHeight: '30px',
  },
});



