import React from 'react';
import glamorous from 'glamorous';

import DisplayText from '../DisplayText';

import ProjectLinks from './ProjectLinks';

import mediaQueries from '../../utilities/mediaQueries';

const Container = glamorous.div({
  alignItems: 'flex-start',
  display: 'flex',
  flexWrap: 'wrap',
});

const TitleContainer = glamorous.div({
  background: 'white',
  flex: '0 0 40%',
  paddingTop: 30,
  position: 'sticky',
  top: 0,
  [mediaQueries.medium]: {
    flex: '1 0 100%',
  },
});

const InfoContainer = glamorous.div({
  flex: '0 0 60%',
  paddingTop: 30,
  '& img': {
    margin: '40px 0',
  },
  [mediaQueries.medium]: {
    flex: '1 0 100%',
  }
});

export default function Project({
  children,
  githubUrl,
  projectUrl,
  title,
}) {
  return (
    <Container>
      <TitleContainer>
        <DisplayText>{title}</DisplayText>
        <ProjectLinks
          projectUrl={projectUrl}
          githubUrl={githubUrl}
        />
      </TitleContainer>
      <InfoContainer>
        {children}
      </InfoContainer>
    </Container>
  );
}



