import React from 'react';
import glamorous from 'glamorous';
import Transition from 'react-transition-group/Transition';

import easings from '../utilities/easings';

const WordContainer = glamorous.span({
  display: 'inline-block',
  overflow: 'hidden',
});

const Word = glamorous.span({
  float: 'left',
  padding: '10px 0',
  transition: `transform 1000ms ${easings.circular}`,
  overflow: 'hidden',
  willChange: 'transform',
}, ({ index, visible, delay = 0 }) => ({
  transitionDelay: `${index * 65 + delay}ms`,
  transform: visible
    ? 'translate3d(0, 0, 0)'
    : 'translate3d(0, 100%, 0)',
}));

export default function PresentingText({
  children,
  startAnimation,
  delay,
}) {
  const components = typeof children === 'string'
    ? children.split(' ')
    : React.Children.toArray(children);

  return (
    <Transition in={startAnimation} appear timeout={300}>
      {(state) => (
        <span>
          {components.map((word, index) => (
            <WordContainer key={`${word}-${index}`}>
              <Word
                index={index}
                visible={state === 'entered'}
                delay={delay}
              >
                  {word}&nbsp;
                </Word>
            </WordContainer>
          ))}
        </span>
      )}
    </Transition>
  );
}



