import React from 'react';
import glamorous from 'glamorous';

import GitHubLogo from '../../images/github-logo.png';

const Container = glamorous.div({
  marginTop: 20,
});

const Link = glamorous.a({
  display: 'inline-block',
  marginRight: 10,
  ':last-of-type': {
    marginRight: 0,
  },
});

const Icon = glamorous.img({
  height: 20,
});

export default function ProjectLinks({
  githubUrl,
  projectUrl,
}) {
  const projectLinkMarkup = projectUrl
    ? (
      <glamorous.Div marginBottom="20px">
        <a href={projectUrl} target="_blank">check it out &rarr;</a>
      </glamorous.Div>
    )
    : null;

  const githubLinkMarkup = githubUrl
    ? (
      <glamorous.Div marginBottom="20px">
        <Link href={githubUrl} target="_blank"><Icon src={GitHubLogo} /></Link>
      </glamorous.Div>
    )
    : null;

  return (
    <Container>
      {projectLinkMarkup}
      <div>
        {githubLinkMarkup}
      </div>
    </Container>
  )
}



